@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap)repeat scroll 0 0 rgba(0, 0, 0 , 0)
/*@import url('https://fonts.googleapis.com/css2?family=Handlee&display=swap');*/

body {
    background: #fff;
}

.title {
    font-size: 5.6rem;
    font-family: 'Lobster', cursive;
}

.wrapper {
    /*background: url("https://images.unsplash.com/photo-1465146633011-14f8e0781093?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3450&q=80");*/
    background: yellowgreen;
    color: #eee;
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    perspective: 1000px;
    perspective-origin: 50% 50%;
    -webkit-animation: animation 100s linear infinite;
            animation: animation 100s linear infinite;
}

@-webkit-keyframes animation {
    100%{
        background-position:0px -3000px;
    }
}

@keyframes animation {
    100%{
        background-position:0px -3000px;
    }
}
.login-container {
    height: 400px;
    width: 350px;
    padding: 20px;
    border-radius: 5px;
    background: #f6fffe;
    position: fixed;
   /* box-shadow: 5px 5px 10px rgba(0,0,0,0.5);*/
}
table {
    font-family: sans-serif;
}
table th, table td {
    padding: .25em .5em;
    text-align: left;
}
table th:nth-child(2), table td:nth-child(2) {
    text-align: right;
}
table td {
    background-color: #eee;
}
table th {
    background-color: #009;
    color: #fff;
}

.zigzag {
    border-collapse: separate;
    border-spacing: .25em 1em;
    color:black;
}
.zigzag tbody tr:nth-child(odd) {
    transform: rotate(2deg);
}
.zigzag thead tr,
.zigzag tbody tr:nth-child(even) {
    transform: rotate(-2deg);
}

#error{
    color:red;
}
